import React, {useState, useEffect} from "react";
import Content from "./style";
import ScrollableAnchor from "react-scrollable-anchor";

import {Splide, SplideSlide} from '@splidejs/react-splide';
import '@splidejs/splide/dist/css/themes/splide-sea-green.min.css';

import automotriz from "images/statics/maquinas/Pulverizador automotriz.jpg";
import colhedora from "images/statics/maquinas/Colhedora de cafe.jpg";
import adubadora from "images/statics/maquinas/Adubadora.jpg";
import barra_acoplado from "images/statics/maquinas/Pulverizador barra acoplado.jpg";
import barra_carreta from "images/statics/maquinas/Pulverizador barra carreta.jpg";
import turbo_acoplado from "images/statics/maquinas/Pulverizador turbo acoplado.jpg";
import turbo_carreta from "images/statics/maquinas/Pulverizador turbo carreta.jpg";
import canhao_acoplado from "images/statics/maquinas/Pulverizador canhao acoplado.png";
import canhao_carreta from "images/statics/maquinas/Pulverizador canhao carreta.jpg";
import especial_acoplado from "images/statics/maquinas/Pulverizador especial acoplado.jpg";
import especial_carreta from "images/statics/maquinas/Pulverizador especial carreta.jpg";

import banner from "images/banner/banner-conheca-tratores.png";
import bannerMobile from "images/banner/BannerJactoNovosTempos.png";

import Card from "components/card";


function ListaMaquinas() {

  const [isMobile, setMobile] = useState(false);
  const [img, setImagem] = useState(null);

  useEffect(() => {
      if (window.innerWidth >= 992) {
              setMobile(false);
              setImagem(banner);
      } else {
              setMobile(true);
              setImagem(bannerMobile);
      }
  }, [window.innerWidth]);


  return (
    <ScrollableAnchor id="maquinas">
      <Content className="col-12 d-flex flex-column align-items-center">
        <img className="banner" src={img} alt=""/>
        { isMobile ? 
          <div className="textos">
            <strong>NOVOS TEMPOS, NOVAS SOLUES.</strong>
            <p>A PRODUTIVIDADE DO SEU AGRO  O <br/>NOSSO OBJETIVO</p>
          </div>
          :
            <>
            <div className="demarcador"></div>
            <div className="textos">
              <div className="demarcadorTexto"></div>
              <strong>NOVOS TEMPOS,<br/>NOVAS SOLUES.</strong>
              <p>A PRODUTIVIDADE DO<br/>SEU AGRO  O NOSSO<br/>OBJETIVO</p>
            </div>
            </>
          }
        <div className="title ">Conhea nossas Mquinas</div>
        { isMobile ?  
              <Splide className="splide col-12" options={{
                  rewind: true,
                  width: "100%",
                  gap: '2rem',
                  perPage: 1,
                  pagination: true,
                  arrows: false
              }}>
                  <SplideSlide className="slide">
                    <Card image={automotriz} title="Pulverizador automotriz" text="Equipamentos para produtores que buscam conforto, qualidade de aplicao e produtividade. Excelente custo/benefcio. Enfrenta os maiores desafios com facilidade e alto desempenho."/>
                  </SplideSlide>
                  <SplideSlide className="slide">
                    <Card image={colhedora} title="Colhedora de caf" text="Qualidade e rendimento para a melhor cafeicultura do mundo. Oferece ao produtor qualidade dos gros colhidos, menos perdas, maior rendimento dirio, conforto e segurana."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                    <Card image={adubadora} title="Adubadora" text="Mquinas versteis que trazem maior preciso na dosagem e uniformidade de aplicao de fertilizantes, alm de possibilitar a aplicao de produtos em p. Qualidade e preciso com a melhor tecnologia Jacto."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                    <Card image={barra_acoplado} title="Pulverizador barra acoplado" text="Qualidade e preciso com a melhor tecnologia Jacto. Desenvolvido para uma pulverizao uniforme, com alto rendimento operacional dirio e custo reduzido de manuteno."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                  <Card image={barra_carreta} title="Pulverizador barra carreta" text="Robustez e versatilidade na proteo da sua lavoura. Com melhor estabilidade para a mquina e consequentemente para a barra."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                  <Card image={turbo_acoplado} title="Pulverizador turbo acoplado" text="Verstil, gil e Compacto. Cobertura Eficiente, atende com a melhor eficincia de aplicao em diversos cultivos e conduo de plantios."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                  <Card image={turbo_carreta} title="Pulverizador turbo carreta" text="Modelos que atendem suas necessidades. Oferecem uma excelente produtividade na pulverizao e protege grandes extenses de cultura em um menor espao de tempo."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                  <Card image={canhao_acoplado} title="Pulverizador canho acoplado" text="Pulverizao de Longo Alcance. Bastante verstil, pode ser utilizado em vrias culturas como: horticultura, floricultura, cereais, pecuria e fruticultura."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                    <Card image={canhao_carreta} title="Pulverizador canho carreta" text="Proteo para sua lavoura. Com vento favorvel, alcana at 35 metros, que proporciona uma boa cobertura nesta faixa de aplicao."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                    <Card image={especial_acoplado} title="Pulverizador especial acoplado" text="Equipamento verstil com baixo custo de manuteno. Maior conforto, desenvolvida com enroladores de mangueira para uma pulverizao uniforme, com alto rendimento operacional."/>
                  </SplideSlide>
                  <SplideSlide className="slide">  
                    <Card image={especial_carreta} title="Pulverizador especial carreta" text="Produtividade para sua Pastagem. Desenvolvido especialmente para pulverizao em pastagens, com alto rendimento operacional dirio e custo reduzido de manuteno."/>         
                  </SplideSlide>
              </Splide>
            
            :
              <div className="maquinas">
                <Card image={automotriz} title="Pulverizador automotriz" text="Equipamentos para produtores que buscam conforto, qualidade de aplicao e produtividade. Excelente custo/benefcio. Enfrenta os maiores desafios com facilidade e alto desempenho."/>
                <Card image={colhedora} title="Colhedora de caf" text="Qualidade e rendimento para a melhor cafeicultura do mundo. Oferece ao produtor qualidade dos gros colhidos, menos perdas, maior rendimento dirio, conforto e segurana."/>
                <Card image={adubadora} title="Adubadora" text="Mquinas versteis que trazem maior preciso na dosagem e uniformidade de aplicao de fertilizantes, alm de possibilitar a aplicao de produtos em p. Qualidade e preciso com a melhor tecnologia Jacto."/>
                <Card image={barra_acoplado} title="Pulverizador barra acoplado" text="Qualidade e preciso com a melhor tecnologia Jacto. Desenvolvido para uma pulverizao uniforme, com alto rendimento operacional dirio e custo reduzido de manuteno."/>
                <Card image={barra_carreta} title="Pulverizador barra carreta" text="Robustez e versatilidade na proteo da sua lavoura. Com melhor estabilidade para a mquina e consequentemente para a barra."/>
                <Card image={turbo_acoplado} title="Pulverizador turbo acoplado" text="Verstil, gil e Compacto. Cobertura Eficiente, atende com a melhor eficincia de aplicao em diversos cultivos e conduo de plantios."/>
                <Card image={turbo_carreta} title="Pulverizador turbo carreta" text="Modelos que atendem suas necessidades. Oferecem uma excelente produtividade na pulverizao e protege grandes extenses de cultura em um menor espao de tempo."/>
                <Card image={canhao_acoplado} title="Pulverizador canho acoplado" text="Pulverizao de Longo Alcance. Bastante verstil, pode ser utilizado em vrias culturas como: horticultura, floricultura, cereais, pecuria e fruticultura."/>
                <Card image={canhao_carreta} title="Pulverizador canho carreta" text="Proteo para sua lavoura. Com vento favorvel, alcana at 35 metros, que proporciona uma boa cobertura nesta faixa de aplicao."/>
                <Card image={especial_acoplado} title="Pulverizador especial acoplado" text="Equipamento verstil com baixo custo de manuteno. Maior conforto, desenvolvida com enroladores de mangueira para uma pulverizao uniforme, com alto rendimento operacional."/>
                <Card image={especial_carreta} title="Pulverizador especial carreta" text="Produtividade para sua Pastagem. Desenvolvido especialmente para pulverizao em pastagens, com alto rendimento operacional dirio e custo reduzido de manuteno."/>         
              </div>
          }
        </Content>
    </ScrollableAnchor>
      
  );
}

export default ListaMaquinas;
