import React from 'react';
import { Button, Col, Modal, Row } from 'react-bootstrap';
import Content from "./style";

import styles from "./styles.css";

import fechar from "images/statics/modalLs/botao-fechar.png";
import foto_serieR from "images/statics/modalLs/serieR.png";
import foto2_serieR from "images/statics/modalLs/serieR2.png";
import foto3_serieR from "images/statics/modalLs/serieR3.png";
import foto_serieH from "images/statics/modalLs/serieH.png";
import foto2_serieH from "images/statics/modalLs/serieH2.png";
import foto3_serieH from "images/statics/modalLs/serieH3.png";
import foto_seriePlus from "images/statics/modalLs/seriePlus.png";
import foto2_seriePlus from "images/statics/modalLs/seriePlus2.png";
import foto3_seriePlus from "images/statics/modalLs/seriePlus3.png";
import foto_serieU from "images/statics/modalLs/serieU.png";
import foto2_serieU from "images/statics/modalLs/serieU2.png";
import foto3_serieU from "images/statics/modalLs/serieU3.png";
import foto_serieG from "images/statics/modalLs/serieG.png";
import foto2_serieG from "images/statics/modalLs/serieG2.png";
import foto3_serieG from "images/statics/modalLs/serieG3.png";
import foto_serieM from "images/statics/modalLs/serieM.png";
import foto2_serieM from "images/statics/modalLs/serieM2.png";
import foto3_serieM from "images/statics/modalLs/serieM3.png";


function ModalTratores(props) {

    let foto, foto2, foto3, name, text1, text2;
    switch(props.name){
      case 'Srie R':
        foto = foto_serieR;
        name = props.name;
        foto2 =  foto2_serieR;
        foto3 =  foto3_serieR;
        text1 = 
          <div> 
            Apesar de compactos, os modelos da srie R da LS Tractor no economizam na potncia
             e eficincia para mdios produtores. Os modelos so ideais para 
             potencializar qualquer produo.<br/><br/>
            Tecnologia, fora e robustez. Sobram adjetivos para as mquinas da srie R.
          </div>
        text2 = 
          <div>
            R65 Cabinado  - R65 Cabinado Estreito<br/>
            R65 Plataformado - R65 Plataformado Estreito<br/>
            R 50 Plataformado<br/>
          </div>
        break;
      case 'Srie H':
        foto = foto_serieH;
        foto2 = foto2_serieH;
        foto3= foto3_serieH;
        name = props.name;
        text1 = 
          <div>
            A principal caracterstica da srie  a sua construo robusta, que confere aos tratores uma grande capacidade de trao. 
            Desenvolvidos para suportarem longas jornadas em operaes severas e uma excelente vida til garantida por um sistema de proteo eletrnica do motor.
            Para mover essas mquinas, adotamos os Motores Perkins, de potncia e torque elevados. Eles foram desenvolvidos para as condies mais severas, impostas pela diversidade da agricultura brasileira.
          </div>
        text2 =   
          <div>
            H Cabinado<br/>
          </div>
        break;
      case 'Srie Plus':
        foto = foto_seriePlus;
        foto2 = foto2_seriePlus;
        foto3= foto3_seriePlus;
        name = props.name;
        text1 = 
          <div>
            Os modelos da LS Tractor Srie Plus, alm de ideais para o trabalho no campo ainda so econmicos, 
            pois consomem pouco combustvel e tambm possuem baixa emisso de poluentes, 
            em comparao com os modelos de marcas concorrentes.<br/><br/>
            Alm disso, demostram excelente desempenho at mesmo em terrenos irregulares. 
            Para os modelos da Srie Plus, qualquer terreno  terreno.<br/><br/>
          </div>
        text2 =   
          <div>
            Plus 80 Cabinado - Plus 90 Cabinado<br/>
            Plus 100 Cabinado - Plus 80 Plataformado<br/>
            Plus 90 Plataformado - Plus 100 Plataformado<br/>
          </div>
        break;
      case 'Srie U':
        foto = foto_serieU;
        foto2 = foto2_serieU;
        foto3= foto3_serieU;
        name = props.name;
        text1 = 
          <div>
            Uma mquina que combina tecnologia de ponta com a fora e a potncia de um motor de alto desempenho. 
            Estes so os modelos da Srie U da LS Tractor, equipados para potencializar os seus resultados de 
            maneira completa e durvel.<br/><br/>
            Seu sistema hidrulico de centro aberto possui vlvulas de controle remoto, atendendo  
            necessidade do condutor a qualquer momento. O motor de 4 cilindros garante alta performance 
            com o mnimo de vibrao e rudos, e a transmisso 16F x 16R. 32F x 16R proporciona versatilidade 
            a qualquer operao.
          </div>
        text2 =   
          <div>
            U 80 Cabinado - U 60 Cabinado<br/>
            U 80 Cabinado - U 60 Cabinado Plataformado<br/>
          </div>
        break;
      case 'Srie G':
        foto = foto_serieG;
        foto2 = foto2_serieG;
        foto3= foto3_serieG;
        name = props.name;
        text1 = 
          <div>
            So projetados para utilizao em mdias e grandes propriedades, 
            com um nvel tecnolgico superior. A TDP Independente  facilmente operada a partir de 
            uma tecla no painel de instrumentos.<br/><br/>
            Alm disso, conta com um motor LS Tractor potente, com estrutura forte e de longa durao. 
            Tudo isso para uma agricultura conectada e um alto padro de conforto.
          </div>
        text2 =   
          <div>
            G40 Plataformado<br/>
          </div>
        break;
      case 'Srie MT1.25':
        foto = foto_serieM;
        foto2 = foto2_serieM;
        foto3= foto3_serieM;
        name = props.name;
        text1 = 
          <div>
            Desenvolvidos para facilitar o trabalho com rapidez e sem esforo, 
            os modelos da srie MT1.25 da LS Tractor proporcionam agilidade na mudana 
            de direo e so equipados com os motores YANMAR, de potncia e desempenho confiveis.<br/><br/>
            Tudo isso demonstrando alta eficincia e baixo consumo de combustvel, 
            alm de possuir a Tecnologia Tier4 - Eco-amigvel.
          </div>
        text2 =   
          <div>
            MT1.25 Plataformado<br/>
          </div>;
        break;
    }

    return (
      <Content>
        <Modal
          {...props}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Body className="p-0">
          <button className="btn-fechar" onClick={props.onHide}><img className="fechar" src={fechar} alt="fechar"/></button>
            <div className="conteudo">
              <div className="image-side col-12 col-md-6" 
                  p={0}
                  style={{backgroundImage: `url(${foto})`}}
              >
                {/* <div>
                    <img className="imagem1" src={foto} alt="Trator LS"/>
                </div> */}
              </div>
              <div className="text-side px-3">
                <div className="topo py-2">
                  <div className="name py-2 col-10">
                    { name }
                  </div>
                  
                </div>
                <div className="title"><strong>Diferenciais</strong></div>
                <div className="texto py-2">
                  { text1 }
                </div>
                <div className="title pb-2"><strong>Modelos</strong></div>
                <div className="texto" id="texto2">
                  {text2}
                </div>
                <div className="row mb-4">
                  <img className="imagem2" src={foto2} alt="" style={{marginRight:"20px"}} />
                  <img className="imagem2" src={foto3} alt=""/>
                </div>
              </div>
            </div>
          </Modal.Body>
        </Modal>
      </Content>
    );
  }

  export default ModalTratores;