import React from "react";
import Content from "./style";
import Fade from "react-reveal/Fade";

import ScrollableAnchor from "react-scrollable-anchor";

function DescriptionWorkWithUs() {
    return (
        <ScrollableAnchor id="">
            <Fade top>
                <Content className="col-12 row">
                    <div className="textos col-10 col-md-7">
                        <span>Na nossa cultura de trabalho, temos como essncia a valorizao e cooperao com a nossa gente. Acreditamos que ao se dedicar aos nossos colaboradores, conseguimos trazer ainda mais qualidade aos dias do nosso negcio, que refletem aos nossos produtos.</span><br/><br/>
                        <span>Se voc chegou at aqui, com toda certeza a nossa empresa chamou sua ateno, e agora chegou a nossa vez de te conhecer.</span><br/><br/>
                        <span>Preencha o formulrio abaixo com seus dados e aguarde o retorno de nossa equipe de Recursos Humanos:</span>
                    </div>
                </Content>
            </Fade>
            
        </ScrollableAnchor>
    );
}

export default DescriptionWorkWithUs;
